.App {
  text-align: center;
}

/* Header & Nav bar */

.App-header{
  background-color: Orange;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 99999;
}

.Header-contents{
  display: flex;
  justify-content: space-between ;
  margin: auto 10px;
}

.Navigator{
  display: flex;
  width: 50%;
  margin: auto 10px;
}

.nav-links{
  display: flex;
  justify-content: space-between;
  width: 100%
}

.mobile-toggle {
  width: 1%
}

/* body and body elements */

.App-body {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  min-height: 91vh;
}

.body-divs{
  margin: 30px 30px;
}

.body-texts{
  font-size: 18px;
  margin-bottom: 25px;
}


/* Resume Page */
.skills--lists{
  display: flex;
  justify-content: space-evenly;
}

.skills---divs{
  text-align: left;
}

ul{
  list-style-type: none;
  margin-left: 0px;
  display: inline;
}

/* Portfolio & apps */
.container-description{
  margin-left: 20%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
}

/* .developer_info {
  display: flex;
} */


.developer-github {
  width: 100%;
  margin: 0px auto;
}

.app-box{
  display: flex;
  margin: 0 auto;
  margin-top: 5px;
  padding-bottom: 15px;
  border-bottom: 3px solid orange;
  width: 80%;
}

.container_wrapper{
  margin: auto auto;
  width: 40%;
  border-radius: 5px;
  position: relative;
}

.container--picture{
  width: 100%
}

.container--overlay{
  position: absolute;
  top: 0 ;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.container--overlay:hover{
  opacity: 1;
  transition: all 250ms ease-in-out;
}

.container---blurb{
  color: white;
  margin: 0px auto;
  width: 80%;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.project-title {
  display: flex;
}

.project-title-frame{
  width: 100%;
}

/* Footer */

.App-footer {
  background-color: orange;
  padding: 15px 15px;
  display: flex;
  justify-content: space-around;
}

/* Pictures and logos */
.logos{
  display: flex;
  justify-content: space-around;
  width: 25%
}

.github-logos-frame{
  /* margin: 0px auto; */

}



/* Links */
.App-link {
  color: #61dafb;
}

.links{
  color: white;
  font-family: copperplate;
  font-size: 25px;
}

a {
  width: 100%;
  color: black
}

a:link{
  text-decoration: none;
  /* color: black; */
}

a:visited{
  text-decoration: none;
  color: black
}

a:active{
  color: white
}

a:hover{
  color: white;
  transition: all 250ms ease-in-out;
}

.github-links {
  border-radius: 5px;
  background: white;
  margin: 20px auto;
  width: 15%;
}


/* Fonts */
h2{
  color: black;
}

h1, h3{
  font-family: 'Rock Salt', cursive;
}

h5{
  color: white;
  margin: 0 0
}

li {
  font-size: 20px;
}

