@media screen and (max-width: 1500px) {
  .container---blurb{
    font-size: 25px
  }
}

@media screen and (max-width: 1200px) {
  .container---blurb{
    font-size: 20px
  }
}

@media screen and (max-width: 1000px) {
  .container---blurb{
    font-size: 17px
  }

  .project-title{
    margin-bottom: 0px;
    display: block
  }

  .project-title-frame{
    width: 100%;
  }
  
}
  

@media screen and (max-width: 950px){

    .Header-contents{
      display: flex;
    }

    .Navigator{
      justify-content: space-around;
      width: 75%

    }
    h2{
      margin: auto 0px;
    }
}

@media screen and (max-width: 850px){
  .container---blurb{
    font-size: 14px
  }

  /* USE BLOCK INSTEAD OF INLINE FOR FLEX FOR BORDER TO SHOW */
  .app-box {
    display: block;
    width: 100%; 
    border-bottom: 3px solid orange;

  }

  .container_wrapper{
    margin: 0px auto;
    width: 75%;
  }

  .container---blurb{
    font-size: 25px
  }

  .container-description{
    margin: 0 auto;
    text-align: center;
  }

  .container--picture{
    width: 100%
  }

  .github-links {
    width: 10%;
  }

}

@media screen and (max-width: 800px) {
  .Navigator{
    display: flex;
    width: 100%;
    margin: 20px auto;
  }

  .Header-contents{
    display: block;
    padding: 25px;
  }

  .footer-font {
    width: 20%
  }

  .icon-align {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

@media screen and (max-width: 700px){
  .footer-font{
    margin: auto 0px;
    width: 30%
  }

  .links{
    margin-bottom: 25px;
  }

  .container-description{
    text-align: center;
    margin: 0 auto;
    display: block;
  }

  .logos {
    justify-content: space-around;
    margin: 0px auto
  }

}

@media screen and (max-width: 600px) {
  .Navigator{
    display: block;
  }

  .container---blurb{
    font-size: 20px
  }

  .Header-contents{
    padding: 2px;
    margin: 0 auto;
  }

  img {
    width: 90%;
    margin: 10px auto;
  }

  .mobile-toggle {
    width: 100%;
    height: 35px;
  }

  .mobile-options {
    background-color: black;
    width: 35px;
    height: 5px;
    margin: 5px auto;
  }

  .nav-links{
    opacity: 0;
    height: 1px;
  }

  .mobile-effect{
    transition: all 0.75s ease-in-out;
    opacity: 1;
    text-align: center;
    line-height: 60px;
    width: 100%;
    display: table;
    margin: 10px auto;
  }

  #mobile-menu.active .mobile-options:nth-child(2){
    transition: all 0.5s ease-in-out;
    opacity: 0;
  }

  #mobile-menu.active .mobile-options:nth-child(1){
    transition: all 0.5s ease-in-out;
    transform: translateY(10px) rotate(45deg);
  }

  #mobile-menu.active .mobile-options:nth-child(3){
    transition: all 0.5s ease-in-out;
    transform: translateY(-10px) rotate(-45deg);
  }  
}

@media screen and (max-width: 500px){
 
  .links{
    margin-bottom: 25px;
  }

  .container---blurb{
    font-size: 20px
  }

  .body-divs{
   margin: 25px auto;
  }

  .body-texts{
    width: 90%;
    margin: auto auto
  }

  .logos {
    width: 60%;
  }

  .logos-icons{
    width: 75%
  }

  .footer-font {
    width: 40%
  }

}

@media screen and (max-width: 400px){
  .container---blurb{
    font-size: 15px
  }

  .logos {
    display: inline;
  }

  .logos-icons {
    width: 25%
  }

  .icon-align {
    margin: 0 auto;
  }

}